import React from 'react';
import ModalAPI from '@material-ui/core/Modal';
import { get } from 'lodash';

const Modal = (props) => {
  const { onClose } = props;
  const [open, setOpen] = React.useState(get(props, 'isVisible', false));

  // const handleOpen = () => {
  //   setOpen(true);
  // };

  const handleClose = () => {
    setOpen(false);
    onClose(false);
  };

  return (
    <div>
      {/* {props.isButtonNeeded &&
        <button type="button" onClick={handleOpen}>
          Open
      </button>} */}
      <ModalAPI
        open={open}
        onClose={handleClose}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
      >
        {props.children}
      </ModalAPI>
    </div>
  );
}

export default Modal;