import React, { useState, useEffect } from 'react';
import { Image, Text, withSitecoreContext } from '@sitecore-jss/sitecore-jss-react';
import Grid from '../../../components/molecules/Grid';
import { COMPONENT_NAME } from './locators';
import useStyles from './style';
import Typography from '../../../components/atoms/Typography';
import Link from '../../../components/atoms/Link';
import Button from '../../../components/atoms/Button';
import DctSvgIcon from '../../../components/atoms/Svg';
import clsx from 'clsx';
import { get } from 'lodash';
import Accordion from '../../../components/atoms/Accordion';
import Box from '../../../components/molecules/Box';
import { modifyApi } from '../../../../utils/urls';
import { formatUEADateTimeToUTC, getDateInFormat } from '../../../utils/dateTimeFunctions/dateTimeFunctions';
import fetchService from '../../../../services/FetchService';
import Modal from '../../../components/atoms/Modal';
import { LANGUAGE_CODE } from '../../../../constants';
import withErrorBoundary from '../../../utils/HOCs/withErrorBoundary';
import { getDictionaryText } from '../../../utils/getDictionaryText';
import { canUseDOM } from '../../../utils/canUseDOM';
import { getStringToShowInCalendar } from '../../../utils/stringFunctions';
import {
  pushAnaylyticsData,
  createDynamicLabels,
} from '../../../../utils/analytics';
import {
  getPageName,
  getComponentName,
} from '../../../utils/getPageComponentInfo';
import { analyticsData } from '../../../../constants/Analytics/analytics-data';

const AddToCalendar = (props) => {
  const { eventStartDate, eventEndDate,
    eventId,
    eventPageCalendarText, eventPageCalendarIcon, calendarType }
    = get(props, 'sitecoreContext.route.fields', {});
  let gmailCalendarIcon, gmailCalendarText, outlookCalendarIcon, outlookCalendarText;
  const GMAIL_TEXT_ICON_KEY = getDictionaryText('AddToCalendar_Gmail_Text');
  const OUTLOOK_TEXT_ICON_KEY = getDictionaryText('AddToCalendar_Outlook_Text');
  calendarType && calendarType.forEach(item => {
    if (get(item, 'fields.calendarText.value', '').toLowerCase() === GMAIL_TEXT_ICON_KEY) {
      gmailCalendarIcon = get(item, 'fields.calendarIcon', '');
      gmailCalendarText = get(item, 'fields.calendarText', '');
    } else if (get(item, 'fields.calendarText.value', '').toLowerCase() === OUTLOOK_TEXT_ICON_KEY) {
      outlookCalendarIcon = get(item, 'fields.calendarIcon', '');
      outlookCalendarText = get(item, 'fields.calendarText', '');
    }
  })
  const { appConfig } = props || {};
  const language = get(props, 'sitecoreContext.language', LANGUAGE_CODE.DEFAULT);
  const [isOverlayVisible, setIsOverlayVisible] = useState(false);
  const [expanded, setExpanded] = useState(0);
  const [eventList, setEventList] = useState();

  const classes = useStyles();

  useEffect(() => {
    getAddToCalendarEventListData && getAddToCalendarEventListData();
  }, []);

  const addAnalyticsInfo = (type, eventName) => {
    let levelThreeText = '';
    if (type === 'outlook') {
      levelThreeText = `${get(analyticsData, 'dataLinksText.outlook', '')}_${eventName}`;
    } else if (type === 'gmail') {
      levelThreeText = `${get(analyticsData, 'dataLinksText.gmail', '')}_${eventName}`;
    } else {
      levelThreeText = `${get(analyticsData, 'dataLinksText.addToCalendar', '')}_${eventName}`;
    }
    const obj = {
      ...analyticsData.pageBodyInteraction.addToCalendar,
      ...{
        label: createDynamicLabels([getPageName(props), getComponentName(props), levelThreeText]),
      },
    };
    pushAnaylyticsData(obj);
  };

  const handleChange = panel => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };

  const getAddToCalendarEventListData = () => {
    try {
      fetchService({
        url: `${appConfig.ADD_TO_CALENDER_API}`,
        method: 'POST',
        data: {
          eventId: eventId && eventId.value,
          language,
        },
      }).then(res => {
        if (get(res, 'data.data')) {
          setEventList(get(res, 'data.data'));
        }
      });
    } catch (error) {
      console.error('Something went wrong with add to calendar api.', error);
    }
  };

  return (
    <Grid className={clsx(classes.root, COMPONENT_NAME)} data-locator={COMPONENT_NAME}>
      <div className={classes.displayArea}>
        <div className={classes.dateTimeRange}>
          <Typography variant="body2Bold" component="p">
            <Text field={{ value: `${getDateInFormat(eventStartDate.value, 'ddd, MMM DD, YYYY hh:mm A')} - ${getDateInFormat(eventEndDate.value, 'ddd, MMM DD, YYYY hh:mm A')}` }} />
          </Typography>
        </div>
        <div className="pipe"></div>
        <div className={classes.addToCalendar}>
          {get(eventList, 'years', []).length > 0 &&
            <div className={classes.textImgAlignment} onClick={() => {
              setIsOverlayVisible(true);
              addAnalyticsInfo('addToCalendar', eventList && getStringToShowInCalendar(eventList.eventName));
            }
            }>
              <Typography variant="body2Bold" component="p">
                <Text field={eventPageCalendarText} />
              </Typography>
              <Image field={eventPageCalendarIcon} />
            </div>}
        </div>
      </div>

      <Modal key={Date.parse(new Date())}
        isVisible={isOverlayVisible}
        isButtonNeeded={false}
        onClose={() => setIsOverlayVisible(false)}
      >
        <Box className={classes.overlayWrapperContent} >
          <Button
            iconOnly
            color="inherit"
            size="small"
            className={clsx('close-btn', 'small')}
            onClick={() => setIsOverlayVisible(false)}
          >
            <DctSvgIcon name="CloseIcon" />
          </Button>
          <Grid className="content">
            {
              get(eventList, 'years', []).map(yearData => {

                return get(yearData, 'months', []).map((monthData, index) => {
                  return (

                    <Accordion
                      className="accordion"
                      key={index}
                      tabIdentifier={index}
                      expanded={expanded}
                      handleChange={handleChange}
                    >
                      <Typography component="p" variant="h5Bold">
                        <Text field={{ value: monthData.MonthName }} />
                      </Typography>
                      <Grid container className='accordionContainer'>
                        {
                          get(monthData, 'scheduleItems', []).map(scheduleItem => {
                            const scheduleDescription = scheduleItem && getStringToShowInCalendar(scheduleItem.description);
                            const ogDescription = canUseDOM && getStringToShowInCalendar(document.querySelector("meta[name='og:description']").getAttribute('content'));
                            const currentPageURL = canUseDOM && window.location.href;

                            const gmailDescriptionHTMLTemplate = `<p>${scheduleDescription}</br></br>${ogDescription}</br></br><a>${currentPageURL}</a></p>`;
                            const outlookDescriptionHTMLTemplate = `${scheduleDescription} \\n\\n ${ogDescription} \\n\\n ${currentPageURL}`;
                            const event = {
                              ...scheduleItem,
                              startDate: formatUEADateTimeToUTC(scheduleItem.startDate),
                              endDate: formatUEADateTimeToUTC(scheduleItem.endDate),
                              title: eventList && getStringToShowInCalendar(eventList.eventName),
                              description: gmailDescriptionHTMLTemplate,
                            }
                            const outlookEvent = ['BEGIN:VCALENDAR',
                              'VERSION:2.0',
                              'BEGIN:VEVENT',
                              `URL:${canUseDOM && window.location.href}`,
                              `DTSTART:${scheduleItem && formatUEADateTimeToUTC(scheduleItem.startDate)}`,
                              `DTEND:${scheduleItem && formatUEADateTimeToUTC(scheduleItem.endDate)}`,
                              `SUMMARY:${eventList && getStringToShowInCalendar(eventList.eventName)}`,
                              `DESCRIPTION:${outlookDescriptionHTMLTemplate}`,
                              `LOCATION:${scheduleItem && scheduleItem.location}`,
                              'END:VEVENT',
                              'END:VCALENDAR'].join('\n');
                            return (
                              <Grid key={JSON.stringify(scheduleItem)} item xs={12} lg={4} className='accordionItem'>
                                <div className={classes.eventCard}>
                                  <Typography variant='body2Bold' component='p'>
                                    <Text field={{ value: `${getDateInFormat(scheduleItem.startDate, 'MMM DD')} - ${getDateInFormat(scheduleItem.endDate, 'MMM DD, YYYY')}, ${getDateInFormat(scheduleItem.startDate, 'HH:mm')} - ${getDateInFormat(scheduleItem.endDate, 'HH:mm')}` }} />
                                  </Typography>
                                  {scheduleItem.location && (
                                    <Typography
                                      variant="body3"
                                      component="div"
                                      classes={{ root: classes.location }}
                                    >
                                      <Box component="span" display="inline-flex">
                                        {scheduleItem.location}
                                      </Box>
                                    </Typography>
                                  )}
                                  <div className={classes.calenderType}>
                                    {get(gmailCalendarText, 'value') && <div className="gmail">
                                      <Link isCustom={true} target="_blank" onClick={(e) => {
                                        e.preventDefault();
                                        window.open(modifyApi(appConfig && appConfig.GOOGLE_ADD_TO_CALENDER_URL, event), '_blank');
                                        addAnalyticsInfo('gmail', event.title);
                                      }}>
                                        <div className={classes.textImgAlignment}>
                                          <Image field={gmailCalendarIcon} />
                                          <Typography variant="body2Bold" component="p">
                                            <Text field={gmailCalendarText} />
                                          </Typography>
                                        </div>
                                      </Link>
                                    </div>}
                                    <div className="outlook">
                                      <Link isCustom={true} customHref="#" onClick={(e) => {
                                        e.preventDefault();
                                        addAnalyticsInfo('outlook', event.title);
                                        window.open('data:text/calendar;charset=utf8,' + unescape(outlookEvent), '_blank');
                                        e.stopPropagation();
                                      }}>
                                        <div className={classes.textImgAlignment}>
                                          <Image field={outlookCalendarIcon} />
                                          <Typography variant="body2Bold" component="p">
                                            <Text field={outlookCalendarText} />
                                          </Typography>
                                        </div>
                                      </Link>
                                    </div>
                                  </div>
                                </div>
                              </Grid>
                            )
                          })
                        }
                      </Grid>
                    </Accordion>
                  )
                })
              })
            }
          </Grid>
        </Box>
      </Modal>
    </Grid>
  );
}

export default withErrorBoundary(
  withSitecoreContext()(AddToCalendar)
);
