import { makeStyles } from '@material-ui/core/styles';
import { pxToRem } from '../../../../styles/themes/vad/constants';

const useStyles = makeStyles(theme => ({
  root: {
    ...theme.shape.componentSpacing,
    marginTop: pxToRem(50),
  },
  displayArea: {
    display: 'flex',
    alignItems: 'center',
    flexDirection: 'column',
    [theme.breakpoints.up('lg')]: {
      flexDirection: 'row',
    },
    '& .pipe': {
      display: 'none',
      [theme.breakpoints.up('lg')]: {
        display: 'block',
        ...theme.mixins.marginRight(pxToRem(24)),
        border: theme.borders.divider,
        background: theme.mixins.Black(200),
        height: pxToRem(24),
        width: pxToRem(2),
      },
    },
  },
  dateTimeRange: {
    alignSelf: 'flex-start',
    ...theme.mixins.marginRight(theme.spacing(0)),
    [theme.breakpoints.up('lg')]: {
      alignSelf: 'center',
      ...theme.mixins.marginRight(theme.spacing(3)),
    },
  },
  addToCalendar: {
    alignSelf: 'flex-start',
    '&>div>p': {
      ...theme.mixins.marginLeft(theme.spacing(0)),
    },
    [theme.breakpoints.down('md')]: {
      marginTop: theme.spacing(2),
    },
  },
  pipe: {
    border: theme.borders.divider,
    background: theme.mixins.Black(200),
    height: pxToRem(24),
    width: pxToRem(2),
  },
  textImgAlignment: {
    cursor: 'pointer',
    display: 'flex',
    flexDirection: 'row',
    alignItems: 'center',
    '& p': {
      ...theme.mixins.marginLR(theme.spacing(2), theme.spacing(2)),
    },
  },
  eventCard: {
    padding: theme.spacing(2),
    border: `1px solid ${theme.mixins.Grey(400)}`,
    marginBottom: theme.spacing(2),
    '&>p': {
      color: theme.mixins.Black(),
    },
  },
  location: {
    display: 'flex',
    '&.MuiTypography-root': {
      color: theme.palette.text.tertiary,
      marginTop: theme.spacing(0.25),
      marginBottom: theme.spacing(1.25),
    },
  },
  calenderType: {
    display: 'flex',
    flexDirection: 'row',
    flexWrap: 'no',
    '& .gmail, .outlook': {
      cursor: 'pointer',
      '& img': {
        height: pxToRem(21),
        width: pxToRem(21),
      },
      '& p': {
        ...theme.mixins.marginLR(pxToRem(10), pxToRem(10)),
      },
    },
    '& .gmail': {
      ...theme.mixins.borderRight(theme.borders.divider),
      ...theme.mixins.marginRight(pxToRem(10)),
    },
    '& .MuiIcon-root': {
      animationName: 'none',
    },
  },
  overlayWrapperContent: {
    position: 'absolute',
    backgroundColor: theme.mixins.White(),
    padding: theme.spacing(10, 0, 0),
    zIndex: 13500,
    border: '0px',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    boxShadow: '0px 2px 30px 0px rgba(0, 0, 0, 0.08)',
    outline: 0,
    width: '100%',
    [theme.breakpoints.up('lg')]: {
      padding: theme.spacing(10, 6, 2),
      width: '70%',
      transform: 'translate(-50%, -50%)',
    },
    '& .close-btn': {
      color: theme.mixins.Black(),
      position: 'absolute',
      top: theme.spacing(3),
      ...theme.mixins.right(theme.spacing(6)),
      zIndex: '10',
      width: pxToRem(40),
      height: pxToRem(40),
      background: theme.mixins.White(900),
      borderRadius: '50%',
      border: theme.borders.divider,
      cursor: 'pointer',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      padding: 0,
      [theme.breakpoints.up('lg')]: {
        top: theme.spacing(4),
        ...theme.mixins.right(theme.spacing(4)),
        width: pxToRem(56),
        height: pxToRem(56),
      },

      '&:hover': {
        backgroundColor: theme.mixins.Black(100),
      },

      '& svg': {
        height: theme.spacing(2),
        width: theme.spacing(2),
        [theme.breakpoints.up('lg')]: {
          height: theme.spacing(4),
          width: theme.spacing(4),
        },
      },

      '&.small': {
        top: theme.spacing(2),
        ...theme.mixins.right(theme.spacing(3)),
        height: theme.spacing(4),
        width: theme.spacing(4),
        [theme.breakpoints.up('lg')]: {
          top: theme.spacing(3),
          ...theme.mixins.right(theme.spacing(3)),
          height: theme.spacing(6),
          width: theme.spacing(6),
        },

        '& svg': {
          [theme.breakpoints.up('lg')]: {
            height: theme.spacing(3),
            width: theme.spacing(3),
          },
        },
      },
    },
    '& .content': {
      //borderTop: theme.borders.divider,
      margin: 0,
      padding: 0,
      overflowY: 'auto',
      maxHeight: '80vh',
    },
    '& .accordion': {
      borderBottom: theme.borders.divider,
      '&>div': {
        width: '100%',
      },
      '& .MuiIconButton-root': {
        padding: 0,
        [theme.breakpoints.down('md')]: {
          ...theme.mixins.marginRight(pxToRem(8)),
        },
      },
      '& .MuiSvgIcon-root': {
        height: pxToRem(32),
        width: pxToRem(32),
        color: theme.mixins.Black(),
      },
    },
    '& .MuiExpansionPanelDetails-root': {
      ...theme.mixins.paddingRight(0),
    },
    '& .accordionItem': {
      ...theme.mixins.marginRight(pxToRem(24)),
      [theme.breakpoints.up('lg')]: {
        ...theme.mixins.marginRight(pxToRem(0)),
        maxWidth: `calc(33.333333% - ${pxToRem(32 / 3 + 0.1)})`,
        '&:nth-child(3n-1)': {
          ...theme.mixins.marginLR(pxToRem(16), pxToRem(16)),
        },
      },
    },
  },
}));

export default useStyles;
